import { useState, useEffect } from 'react';
import egg from '../../assects/dragon-egg.png';
import eggHead from '../../assects/egg-head.png';
import eggTail from '../../assects/egg-tail.png';
import wenhao from '../../assects/wenhao.png';
import pets from '../../pets'
import './egg.css';
function getBase64(url, fn) {
    var Img = new Image(),
        dataURL = '';
    Img.src = url;
    Img.onload = function () {
        var canvas = document.createElement('canvas'),
            width = Img.width,
            height = Img.height;
        canvas.width = width;
        canvas.height = height;
        let ctx = canvas.getContext('2d');
        ctx.drawImage(Img, 0, 0, width, height);
        let imgData = ctx.getImageData(0, 0, canvas.width, canvas.height);
        let data = imgData.data;
        let randBaseR = Math.floor((Math.random() * 255 + 1));
        let randBaseB = Math.floor((Math.random() * 255 + 1));
        let randBaseG = Math.floor((Math.random() * 255 + 1));
        for (let i = 0; i < data.length; i += 4) {
            if (data[i] + data[i + 1] + data[i + 2] > 700) {
                randBaseR += Math.floor((Math.random() * 3) - 1);
                randBaseB += Math.floor((Math.random() * 3) - 1);
                randBaseG += Math.floor((Math.random() * 3) - 1);
                data[i] = randBaseR + Math.floor((Math.random() * 60) - 30);
                data[i + 1] = randBaseB + Math.floor((Math.random() * 60) - 30);
                data[i + 2] = randBaseG + Math.floor((Math.random() * 60) - 30);
            }
        }
        ctx.data = data;
        ctx.putImageData(imgData, 0, 0);
        dataURL = canvas.toDataURL('image/png');
        fn(dataURL);
    };
}
function Egg() {
    let [time, setTime] = useState(null);
    let [isfu, setIsfu] = useState(true);
    let [isbreak, setIsbreak] = useState(false);
    let [mon, setMon] = useState(null);
    let [adapt, setAdapt] = useState(false);
    let [monProps, setMonProps] = useState([]);
    let [name, setName] = useState("");
    let [id, setId] = useState("");
    let [startTime, setStartTime] = useState("");
    let [mons, setMons] = useState(JSON.parse(localStorage.getItem("pets")));
    useEffect(() => {
        let time = localStorage.getItem("egg-start-time") || new Date().getTime();
        localStorage.setItem("egg-start-time", time);
        setStartTime(time);
        setTime(Math.min((new Date().getTime() - time) / 1000, 18000));
        let a = [];
        for (let m in mons) {
            a.push({ id: m, ...mons[m] });
        }
        setMons(a);
    }, []);
    useEffect(() => {
        setTimeout(
            () => {
                isfu &&
                    setTime(Math.min((new Date().getTime() - startTime) / 1000, 18000));
            }, 1000);

        return () => {

        }
    });


    let handleFuhua = () => {
        if (!isbreak) {
            setIsbreak(true);
            setIsfu(false);
        } else {
            let level = localStorage.getItem("level")
            let monProps = [
                Math.ceil((Math.random() * 100 * level) + 50 * level),
                Math.ceil(Math.random() * 100 * level + 50 * level),
                Math.ceil(Math.random() * 100 * level + 50 * level)
            ];
            setMonProps(monProps);
            setIsbreak(false);
            getBase64(pets[Math.floor(Math.random() * pets.length)], (e) => {
                setMon(e);
                let id = Math.ceil(Math.random() * 200000000);
                setId(id);
                let pets = JSON.parse(localStorage.getItem("pets")) || {};
                pets[id] = { name, monProps, dataURL: e };
                localStorage.setItem('pets', JSON.stringify(pets));
            });
            setAdapt(true);
        }
    }
    return (
        <div className="egg-container">
            {
                isfu &&
                <>
                    <img className="eggimg" src={egg} alt="egg" />
                </> ||
                isbreak &&
                <div className="breakegg">
                    <img className="eggimg egg-head" src={eggHead} alt="" />
                    <img className="eggimg" src={eggTail} alt="" />
                    <img className="eggimg" src={wenhao} alt="" />
                </div>
            }
            <progress className="loading" value={time} max="18000"></progress>
            <div className="loadnum" >{(((time || 0) / 18000) * 100).toFixed(2) + '%'}</div>
            <div>{time === 18000 ? "" : "剩余时间：" + ((18000 - (time || 0)) / 60).toFixed(1) + 'min'}</div>
            <button className="eggbtn"
                style={{ opacity: time < 18000 ? 0 : 1 }} onClick={handleFuhua}
                disabled={time < 18000}
            >{isbreak ? '领养~~' : '破壳~~'}</button>

            {/*这是领养时的弹窗*/}
            {
                adapt &&
                <div className="mon">
                    <img className="monimg" src={mon} alt="123" />
                    <div>名字：<input className="nameinput" type="text" maxLength="8" onChange={(e) => { setName(e.target.value) }} /></div>
                    <button className="eggbtn"
                        onClick={() => {
                            setTime(0);
                            setAdapt(false);
                            let cur = new Date().getTime();
                            localStorage.setItem("egg-start-time", cur);
                            setStartTime(cur)
                            setIsfu(true);
                            let pets = JSON.parse(localStorage.getItem("pets")) || {};
                            pets[id]['name'] = name;
                            localStorage.setItem('pets', JSON.stringify(pets));
                        }}>收下啦！！！</button>
                </div>
            }


        </div>
    )
}
export default Egg;
